/* Timeline panel container */
.timeline-panel-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1e1e1e;
  overflow: hidden;
}

/* Timeline content */
.timeline-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

/* Timeline toolbar */
.timeline-toolbar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  background-color: #2a2a2a;
  border-bottom: 1px solid #3a3a3a;
  flex-shrink: 0;
}

.timeline-button {
  background-color: #333;
  border: 1px solid #4a4a4a;
  color: #e0e0e0;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 0;
}

.timeline-button:hover {
  background-color: #404040;
  border-color: #5a5a5a;
}

.timeline-icon {
  font-size: 10px;
  display: block;
}

.timeline-frame-info {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-left: auto;
  background-color: #1a1a1a;
  padding: 4px 12px;
  border-radius: 4px;
  border: 1px solid #3a3a3a;
  flex-shrink: 0;
}

.frame-label {
  font-size: 12px;
  color: #999;
}

.frame-input {
  width: 50px;
  padding: 2px 6px;
  background-color: #2a2a2a;
  border: 1px solid #3a3a3a;
  color: #e0e0e0;
  border-radius: 3px;
  font-size: 12px;
  text-align: center;
}

.frame-input:focus {
  outline: none;
  border-color: #0066cc;
}

.frame-separator {
  color: #666;
  font-size: 12px;
}

.frame-total {
  color: #999;
  font-size: 12px;
}

/* Timeline tracks container */
.timeline-tracks-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  width: 100%;
}

/* Timeline ruler */
.timeline-ruler-container {
  display: flex;
  height: 30px;
  background-color: #252525;
  border-bottom: 1px solid #3a3a3a;
  flex-shrink: 0;
}

.ruler-header {
  width: 120px;
  min-width: 120px;
  background-color: #252525;
  border-right: 1px solid #3a3a3a;
  flex-shrink: 0;
}

.timeline-ruler {
  flex: 1;
  overflow-x: auto;
  overflow-y: hidden;
  position: relative;
}

.timeline-ruler::-webkit-scrollbar {
  height: 0;
}

.ruler-content {
  position: relative;
  height: 30px;
  min-width: 2410px; /* 241 ticks * 10px */
  background-color: #252525;
}

.ruler-mark {
  position: absolute;
  top: 0;
  width: 10px;
  height: 100%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.ruler-tick {
  background-color: #4a4a4a;
  width: 1px;
}

.ruler-tick.major {
  height: 12px;
  background-color: #6a6a6a;
}

.ruler-tick.minor {
  height: 6px;
}

.ruler-number {
  font-size: 10px;
  color: #999;
  margin-bottom: 3px;
}

.ruler-mark:hover .ruler-tick {
  background-color: #8a8a8a;
}

/* Timeline tracks */
.timeline-tracks {
  flex: 1;
  overflow: auto;
  position: relative;
  background-color: #1e1e1e;
  padding-left: 120px;
  box-sizing: content-box;
  min-width: 2410px; /* Match ruler width */
}

.timeline-tracks::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.timeline-tracks::-webkit-scrollbar-track {
  background: #1a1a1a;
}

.timeline-tracks::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 4px;
}

.timeline-tracks::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

/* Individual tracks */
.timeline-track {
  display: flex;
  height: 40px;
  border-bottom: 1px solid #2a2a2a;
}

.track-header {
  position: absolute;
  left: 0;
  width: 120px;
  min-width: 120px;
  padding: 0 10px;
  background-color: #252525;
  border-right: 1px solid #3a3a3a;
  display: flex;
  align-items: center;
  height: 40px;
  z-index: 2;
}

.track-name {
  font-size: 12px;
  color: #999;
  font-weight: 500;
}

.track-content {
  flex: 1;
  position: relative;
  background: repeating-linear-gradient(
    90deg,
    transparent,
    transparent 9px,
    #2a2a2a 9px,
    #2a2a2a 10px
  );
}

.track-keys {
  position: relative;
  height: 100%;
}

/* Keyframes */
.keyframe {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  width: 8px;
  height: 8px;
  background-color: #0066cc;
  border: 1px solid #0077dd;
  cursor: grab;
  transition: all 0.2s;
  z-index: 3;
}

.keyframe:hover {
  background-color: #0077dd;
  border-color: #0088ee;
  transform: translateY(-50%) rotate(45deg) scale(1.2);
}

.keyframe.dragging {
  cursor: grabbing;
  background-color: #ff9800;
  border-color: #ffaa00;
  z-index: 10;
}

/* Timeline scrubber */
.timeline-scrubber {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 21px; /* Increased width for better mouse picking */
  cursor: ew-resize;
  z-index: 20;
  pointer-events: none;
}

.timeline-scrubber.dragging {
  z-index: 25;
}

.scrubber-head {
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 8px solid #ff4444;
  pointer-events: all;
  cursor: ew-resize;
  z-index: 30;
}

.scrubber-head::after {
  content: '';
  position: absolute;
  bottom: 8px;
  left: -7px;
  width: 14px;
  height: 4px;
  background-color: #ff4444;
  border-radius: 2px;
}

.scrubber-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  background-color: #ff4444;
  pointer-events: none;
}

.timeline-scrubber:hover .scrubber-head,
.timeline-scrubber:hover .scrubber-head::after,
.timeline-scrubber:hover .scrubber-line,
.timeline-scrubber.dragging .scrubber-head,
.timeline-scrubber.dragging .scrubber-head::after,
.timeline-scrubber.dragging .scrubber-line {
  background-color: #ff6666;
  border-top-color: #ff6666;
}

/* Light theme adjustments */
body.light-theme .timeline-panel-container {
  background-color: #f5f5f5;
}

body.light-theme .timeline-toolbar {
  background-color: #ffffff;
  border-bottom-color: #e0e0e0;
}

body.light-theme .timeline-button {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
  color: #333333;
}

body.light-theme .timeline-button:hover {
  background-color: #e8e8e8;
  border-color: #c0c0c0;
}

body.light-theme .timeline-frame-info {
  background-color: #f8f8f8;
  border-color: #e0e0e0;
}

body.light-theme .frame-label,
body.light-theme .frame-separator,
body.light-theme .frame-total {
  color: #666666;
}

body.light-theme .frame-input {
  background-color: #ffffff;
  border-color: #d0d0d0;
  color: #333333;
}

body.light-theme .frame-input:focus {
  border-color: #0066cc;
}

body.light-theme .timeline-ruler-container,
body.light-theme .ruler-header,
body.light-theme .ruler-content {
  background-color: #f0f0f0;
  border-color: #d0d0d0;
}

body.light-theme .track-header {
  background-color: #f0f0f0;
  border-color: #d0d0d0;
}

body.light-theme .track-name {
  color: #666666;
}

body.light-theme .timeline-tracks {
  background-color: #ffffff;
}

body.light-theme .timeline-track {
  border-bottom-color: #e0e0e0;
}

body.light-theme .track-content {
  background: repeating-linear-gradient(
    90deg,
    transparent,
    transparent 9px,
    #f0f0f0 9px,
    #f0f0f0 10px
  );
}

body.light-theme .ruler-tick {
  background-color: #c0c0c0;
}

body.light-theme .ruler-tick.major {
  background-color: #a0a0a0;
}

body.light-theme .ruler-number {
  color: #666666;
}

body.light-theme .ruler-mark:hover .ruler-tick {
  background-color: #808080;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-track {
  background: #f5f5f5;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-thumb {
  background: #c0c0c0;
}

body.light-theme .timeline-tracks::-webkit-scrollbar-thumb:hover {
  background: #b0b0b0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .timeline-toolbar {
    padding: 8px;
    gap: 6px;
  }

  .timeline-button {
    padding: 4px 8px;
    font-size: 11px;
  }

  .timeline-icon {
    font-size: 12px;
  }

  .ruler-header,
  .track-header {
    width: 80px;
    min-width: 80px;
  }

  .timeline-tracks {
    padding-left: 80px;
  }

  .frame-input {
    width: 40px;
  }
}