.results-panel {
  display: flex;
  height: 100%;
  background-color: #1e1e1e;
  color: #e0e0e0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

/* Results Content Container */
.results-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;  /* For fullscreen positioning */
}

/* Top Bar */
.results-top-bar {
  background-color: #242424;  /* Same as tab color */
  padding: 4px 12px;
  border-bottom: 1px solid #3a3a3a;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
  height: 32px;  /* Narrower height */
}

.top-bar-left {
  display: flex;
  align-items: center;
}

.results-title {
  font-size: 14px;
  font-weight: 600;
  color: #e0e0e0;
}

.top-bar-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.scale-label {
  font-size: 12px;
  color: #999;
}

.scale-slider {
  width: 120px;
  height: 4px;
  -webkit-appearance: none;
  appearance: none;
  background: #3a3a3a;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.scale-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 14px;
  background: #0066cc;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s;
}

.scale-slider::-webkit-slider-thumb:hover {
  background: #0080ff;
  transform: scale(1.1);
}

.scale-slider::-moz-range-thumb {
  width: 14px;
  height: 14px;
  background: #0066cc;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
}

.scale-slider::-moz-range-thumb:hover {
  background: #0080ff;
  transform: scale(1.1);
}

.scale-value {
  font-size: 12px;
  color: #e0e0e0;
  min-width: 35px;
  text-align: right;
  font-family: 'Consolas', 'Monaco', monospace;
}

/* Version List */
.version-list {
  width: 200px;
  background-color: #252525;
  border-right: 1px solid #3a3a3a;
  display: flex;
  flex-direction: column;
}

.version-list-header {
  padding: 4px 12px;
  background-color: #242424;  /* Same as results-top-bar */
  border-bottom: 1px solid #3a3a3a;
  font-weight: 600;
  font-size: 13px;
  color: #e0e0e0;  /* Same as results-title */
  display: flex;
  align-items: center;
  height: 32px;  /* Narrower height to match */
}

.version-items {
  flex: 1;
  overflow-y: auto;
}

.version-item {
  padding: 6px 12px;
  cursor: pointer;
  border-bottom: 1px solid #2a2a2a;
  transition: background-color 0.2s;
}

.version-item:hover {
  background-color: #2a2a2a;
}

.version-item.selected {
  background-color: #0066cc20;
  border-left: 3px solid #0066cc;
  padding-left: 9px;
}

.version-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.version-main {
  flex: 1;
  min-width: 0;
}

.version-header {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 2px;
}

.version-tag {
  display: inline-block;
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 9px;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  line-height: 1;
}

.version-name {
  font-size: 12px;
  font-weight: 600;
  color: #4a9eff;
}

.version-details {
  display: flex;
  gap: 8px;
  align-items: center;
}

.version-time {
  font-size: 10px;
  color: #888;
}

.version-info {
  font-size: 10px;
  color: #666;
}

.version-delete {
  width: 20px;
  height: 20px;
  border: none;
  background: transparent;
  color: #666;
  font-size: 20px;
  line-height: 1;
  cursor: pointer;
  padding: 0;
  opacity: 0;
  transition: opacity 0.2s, color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.version-item:hover .version-delete {
  opacity: 1;
}

.version-delete:hover {
  color: #ff4444;
}

.version-empty {
  padding: 20px;
  text-align: center;
  color: #666;
  font-size: 12px;
}

/* Visualization Container */
.visualization-container {
  flex: 1;
  padding: 20px;
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  background-color: #1a1a1a;
  align-content: flex-start;
  justify-content: flex-start;
  position: relative;  /* For absolute positioning of fullscreen items */
}

/* Plot Wrapper */
.plot-wrapper {
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.grid-item {
  background-color: #242424;
  border: 1px solid #3a3a3a;
  border-radius: 6px;
  padding: 16px;
  overflow: visible;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;
  position: relative;
  box-sizing: border-box;
}

/* Plot Top Bar */
.plot-top-bar {
  height: 32px;  /* Same height as main topbar */
  background-color: #2a2a2a;
  border-bottom: 1px solid #383838;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 8px;
  margin: -16px -16px 12px -16px;  /* Negative margin to extend to edges */
  border-radius: 6px 6px 0 0;
}

.plot-top-bar-left {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
  min-width: 0;  /* Allow flex item to shrink below content size */
  overflow: hidden;
}

.plot-top-bar-title {
  font-size: 12px;
  font-weight: 600;
  color: #e0e0e0;
  margin-right: auto;  /* Push controls to the right */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 0;  /* Allow shrinking */
  max-width: 150px;  /* Limit width to prevent overlapping with controls */
}

.plot-top-bar-controls {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-right: 8px;
}

.plot-dropdown {
  padding: 3px 20px 3px 8px;
  font-size: 11px;
  background-color: #1a1a1a;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='10' height='6' viewBox='0 0 10 6'%3e%3cpath fill='%23999' d='M0 0l5 5 5-5z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 6px center;
  background-size: 10px 6px;
  border: 1px solid #3a3a3a;
  border-radius: 3px;
  color: #b0b0b0;
  cursor: pointer;
  outline: none;
  transition: all 0.2s;
  min-width: 80px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.plot-dropdown:hover {
  background-color: #222;
  border-color: #444;
  color: #e0e0e0;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='10' height='6' viewBox='0 0 10 6'%3e%3cpath fill='%23e0e0e0' d='M0 0l5 5 5-5z'/%3e%3c/svg%3e");
}

.plot-dropdown:focus {
  border-color: #0066cc;
  box-shadow: 0 0 0 1px rgba(0, 102, 204, 0.3);
}

.plot-input {
  width: 60px;
  padding: 3px 6px;
  font-size: 11px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 3px;
  color: #e0e0e0;
  outline: none;
  transition: all 0.2s;
  text-align: right;
  font-family: 'Consolas', 'Monaco', monospace;
}

.plot-input:hover {
  background-color: #222;
  border-color: #444;
}

.plot-input:focus {
  border-color: #0066cc;
  box-shadow: 0 0 0 1px rgba(0, 102, 204, 0.3);
  background-color: #252525;
}

.plot-input::placeholder {
  color: #666;
  font-style: italic;
}

.plot-top-bar-buttons {
  display: flex;
  gap: 4px;
}

.plot-bar-btn {
  width: 22px;
  height: 22px;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid #3a3a3a;
  border-radius: 3px;
  color: #999;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  transition: all 0.2s;
}

.plot-bar-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #e0e0e0;
  border-color: #4a4a4a;
  transform: translateY(-1px);
}

.plot-bar-btn.active {
  background-color: #0066cc;
  color: white;
  border-color: #0066cc;
}

/* Fullscreen in Container Mode */
.plot-wrapper.fullscreen-in-container {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100% !important;
  height: 100% !important;
  z-index: 50;
  padding: 0;
}

.plot-wrapper.fullscreen-in-container .grid-item {
  width: 100% !important;
  height: 100% !important;
  max-width: none !important;
  max-height: none !important;
  margin: 0;
  border-radius: 0;
}

.plot-wrapper.fullscreen-in-container .plot-content {
  width: 100% !important;
  height: calc(100% - 44px) !important;  /* Full height minus topbar */
}

.plot-wrapper.fullscreen-in-container .heatmap-container,
.plot-wrapper.fullscreen-in-container .signal-plot-container,
.plot-wrapper.fullscreen-in-container .table-container {
  height: 100% !important;
}

.plot-wrapper.fullscreen-in-container .heatmap-canvas {
  width: 600px !important;  /* Larger canvas in fullscreen */
  height: 600px !important;
}

.plot-wrapper.fullscreen-in-container .heatmap-main {
  padding: 20px;
}

.visualization-container .fullscreen-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 45;
}

/* Heatmap Styles */
.heatmap-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
  width: 100%;
  height: 100%;
}

/* Removed old plot-header styles - now using plot-top-bar */

.heatmap-main {
  display: flex;
  flex: 1;
  gap: 12px;
  min-height: 0;
  overflow: visible;  /* Changed from hidden to visible */
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.canvas-wrapper {
  position: relative;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 0;  /* Allow shrinking */
  overflow: hidden;
}

.heatmap-canvas {
  border: 1px solid #3a3a3a;
  cursor: grab;
  background-color: #1a1a1a;
}

.heatmap-canvas:active {
  cursor: grabbing;
}

.tooltip {
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 11px;
  font-family: 'Consolas', 'Monaco', monospace;
  white-space: nowrap;
  border: 1px solid #4a4a4a;
  z-index: 1000;
}

/* Vertical Colorbar */
.colorbar-vertical {
  display: flex;
  flex-direction: row;  /* Changed to row for horizontal layout */
  align-items: center;
  width: 50px;
  flex-shrink: 0;  /* Prevent shrinking */
}

.colorbar-gradient-vertical {
  width: 20px;
  height: 80%;  /* Relative height */
  max-height: 250px;
  background: linear-gradient(to bottom,
    hsl(0, 100%, 50%),     /* Red */
    hsl(60, 100%, 50%),   /* Yellow */
    hsl(120, 100%, 50%),  /* Green */
    hsl(180, 100%, 50%),  /* Cyan */
    hsl(240, 100%, 50%)  /* Blue */
  );
  border: 1px solid #3a3a3a;
  border-radius: 2px;
}

.colorbar-vertical .colorbar-labels {
  height: 80%;  /* Match gradient height */
  max-height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 4px;  /* Changed from margin-right */
  font-size: 10px;
  color: #888;
}

/* Plot Content Container */
.plot-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  height: calc(100% - 32px); /* Subtract topbar height */
}

/* Signal Plot Styles */
.signal-plot-container {
  flex: 1;
  position: relative;
  min-height: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Light Theme Support */
.light-theme .results-panel {
  background-color: #f5f5f5;
  color: #333;
}

.light-theme .results-top-bar {
  background-color: #f8f8f8;
  border-bottom-color: #e0e0e0;
}

.light-theme .results-title {
  color: #333;
}

.light-theme .scale-label {
