}

.light-theme .scale-slider {
  background: #e0e0e0;
}

.light-theme .scale-slider::-webkit-slider-thumb {
  background: #0066cc;
}

.light-theme .scale-slider::-webkit-slider-thumb:hover {
  background: #0080ff;
}

.light-theme .scale-slider::-moz-range-thumb {
  background: #0066cc;
}

.light-theme .scale-slider::-moz-range-thumb:hover {
  background: #0080ff;
}

.light-theme .scale-value {
  color: #333;
}

.light-theme .version-list {
  background-color: #ffffff;
  border-right-color: #e0e0e0;
}

.light-theme .version-list-header {
  background-color: #f8f8f8;
  border-bottom-color: #e0e0e0;
  color: #333;  /* Same as light-theme results-title */
