}

.light-theme .version-item:hover {
  background-color: #f8f8f8;
}

.light-theme .version-item.selected {
  background-color: #0066cc15;
}

.light-theme .version-name {
  color: #333;
}

.light-theme .version-time {
  color: #999;
}

.light-theme .visualization-container {
  background-color: #f0f0f0;
}

.light-theme .grid-item {
  background-color: #ffffff;
  border-color: #e0e0e0;
}

/* Light Theme for Plot Top Bar */
.light-theme .plot-top-bar {
  background-color: #f0f0f0;
  border-bottom-color: #e0e0e0;
}

.light-theme .plot-top-bar-title {
