.properties-content {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0;  /* Remove padding since header bar needs full width */
  width: 100% !important;
  min-width: 0 !important;
  max-width: 100% !important;
  box-sizing: border-box;
}

/* Object Header Bar */
.properties-header-bar {
  background-color: #242424;  /* Match tab color */
  padding: 4px 16px;  /* Reduced vertical padding from 12px to 8px */
  border-bottom: 1px solid #3a3a3a;
  margin-bottom: 8px;
  position: sticky;
  top: 0;
  z-index: 10;
}

.header-bar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.object-type {
  font-size: 11px;
  color: #999;
  font-style: italic;
  padding: 2px 8px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  text-transform: capitalize;
}

.object-name {
  font-size: 14px;
  font-weight: 600;
  color: #e0e0e0;
  cursor: pointer;
  user-select: none;
  padding: 2px 4px;
  border-radius: 3px;
  transition: background-color 0.2s;
}

.object-name:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.header-name-input {
  font-size: 14px;
  font-weight: 600;
  color: #e0e0e0;
  background-color: #1a1a1a;
  border: 1px solid #0066cc;
  border-radius: 3px;
  padding: 2px 4px;
  outline: none;
  font-family: inherit;
  min-width: 100px;
}

.header-tag-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header-tag-label {
  font-size: 12px;
  color: #999;
  font-weight: 500;
}

.header-tag-input {
  flex: 0 0 auto;
  width: 100px;
  padding: 4px 8px;
  border: 1px solid transparent;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.05);
  color: #f0f0f0;
  font-size: 12px;
  font-family: 'Consolas', 'Monaco', monospace;
  transition: all 0.2s ease;
  text-align: left;
}

.header-tag-input:hover {
  border-color: #4a4a4a;
  background-color: rgba(255, 255, 255, 0.08);
}

.header-tag-input:focus {
  outline: none;
  border-color: #0066cc;
  background-color: #1a1a1a;
}

/* Adjust section padding */
.properties-content > .property-section,
.properties-content > .add-component-section {
  padding: 0 10px;
}


.no-selection {
  text-align: center;
  color: #666;
  padding: 20px;
  font-style: italic;
}

.property-section {
  margin-bottom: 8px;
  width: 100%;
  min-width: 0;
}

.section-header {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #e0e0e0;
}

/* Foldout header styles */
.property-section-header {
  display: flex;
  align-items: center;
  padding: 6px 8px;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.2s;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.3);
}

.property-section-header:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.property-section-toggle {
  font-size: 10px;
  margin-right: 8px;
  transition: transform 0.2s ease;
  color: #999;
}

.property-section-toggle.expanded {
  transform: rotate(90deg);
}

.property-section-title {
  font-size: 14px;
  font-weight: 600;
  color: #e0e0e0;
}

.property-section-content {
  padding: 8px 8px 0 8px;
}

.property-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 13px;
  width: 100%;
  min-width: 0;
}

.property-label {
  font-size: 12px;
  color: #b0b0b0;
  margin-bottom: 4px;
  display: block;
}

.property-value {
  color: #e0e0e0;
}

.property-group {
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  min-width: 0;
}

.property-group:last-child {
  margin-bottom: 0;
}

.property-group > .property-label {
  flex: 0 0 auto;
  min-width: 80px;  /* Increased for better alignment */
  width: 80px;      /* Fixed width for all labels */
  margin-bottom: 0;
  font-size: 12px;
  text-align: left;  /* Left-align as requested */
  padding-right: 8px;
  position: relative;  /* For the separator pseudo-element */
}

/* Add a visual separator using pseudo-element */
.property-group > .property-label::after {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 20px;
  background-color: #3a3a3a;
  margin-left: 8px;
  opacity: 0.3;
}

.vector-input {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3px;
  flex: 1;
}

.vector-input input {
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  color: #e0e0e0;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 12px;
  width: 100%;
}

.vector-input input:focus {
  outline: none;
  border-color: #0066cc;
}

.color-input {
  flex: 1;
  height: 28px;
  background-color: #1a1a1a;
  border: 1px solid #3a3a3a;
  border-radius: 3px;
  cursor: pointer;
}

.material-input-group {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 8px;
}

.slider-input {
  flex: 1;
  appearance: none;
  height: 4px;
  background: #3a3a3a;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.slider-input:hover {
  opacity: 1;
}

.slider-input::-webkit-slider-thumb {
  appearance: none;
  width: 12px;
  height: 12px;
  background: #0066cc;
  cursor: pointer;
  border-radius: 50%;
}

.slider-input::-moz-range-thumb {
  width: 12px;
  height: 12px;
  background: #0066cc;
  cursor: pointer;
  border-radius: 50%;
}

.slider-value {
  font-size: 11px;
  color: #b0b0b0;
  flex: 0 0 auto;
  min-width: 32px;
  text-align: right;
}

.properties-content::-webkit-scrollbar {
  width: 8px;
}

.properties-content::-webkit-scrollbar-track {
  background: #1a1a1a;
}

.properties-content::-webkit-scrollbar-thumb {
  background: #4a4a4a;
  border-radius: 4px;
}

.properties-content::-webkit-scrollbar-thumb:hover {
  background: #5a5a5a;
}

/* Light theme adjustments */
.light-theme .properties-header-bar {
  background-color: #f0f0f0;  /* Light tab color */
  border-bottom-color: #d0d0d0;
}

.light-theme .object-name {
  color: #333333;
}

.light-theme .object-name:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.light-theme .header-name-input {
  color: #333333;
  background-color: #ffffff;
  border-color: #0066cc;
}

.light-theme .object-type {
  color: #666666;
  background-color: rgba(0, 0, 0, 0.05);
}

.light-theme .header-tag-label {
  color: #666;
}

.light-theme .header-tag-input {
  background-color: rgba(0, 0, 0, 0.03);
  color: #333333;
}

.light-theme .header-tag-input:hover {
  border-color: #d0d0d0;
  background-color: rgba(0, 0, 0, 0.05);
}

.light-theme .header-tag-input:focus {
  border-color: #0066cc;
  background-color: #ffffff;
}

.light-theme .no-selection {
  color: #999999;
}


.light-theme .section-header {
  color: #333333;
}

.light-theme .property-section-header {
  background-color: rgba(0, 0, 0, 0.08);
}

.light-theme .property-section-header:hover {
  background-color: rgba(0, 0, 0, 0.12);
}

.light-theme .property-section-toggle {
  color: #666;
}

.light-theme .property-section-title {
  color: #333333;
}

.light-theme .property-label {
  color: #666666;
}

.light-theme .property-group > .property-label::after {
  background-color: #d0d0d0;
}

.light-theme .property-value {
  color: #333333;
}

.light-theme .vector-input input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .vector-input input:focus {
  border-color: #0066cc;
}

.light-theme .color-input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
}

.light-theme .slider-input {
  background: #d0d0d0;
}

.light-theme .slider-input::-webkit-slider-thumb {
  background: #0066cc;
}

.light-theme .slider-input::-moz-range-thumb {
  background: #0066cc;
}

.light-theme .slider-value {
  color: #666666;
}

.light-theme .properties-content::-webkit-scrollbar-track {
  background: #f5f5f5;
}

.light-theme .properties-content::-webkit-scrollbar-thumb {
  background: #c0c0c0;
}

.light-theme .properties-content::-webkit-scrollbar-thumb:hover {
  background: #b0b0b0;
}

/* Quaternion display styles */
.quaternion-input {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4px;
  width: 100%;
}

.quaternion-value {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.quaternion-value label {
  font-size: 10px;
  color: #999;
  font-weight: 600;
}

.quaternion-value input {
  width: 100%;
  text-align: center;
}

.readonly-input {
  background-color: #2a2a2a !important;
  border: 1px solid #444444 !important;
  color: #cccccc !important;
  padding: 6px 12px !important;
  border-radius: 3px !important;
  font-size: 12px !important;
  flex: 1 !important;
  text-align: center !important;
  cursor: default !important;
  min-height: 24px !important;
  box-sizing: border-box !important;
}

.readonly-input:focus {
  outline: none !important;
  border-color: #444444 !important;
}

/* Additional styles for ComponentRenderer */
.checkbox-input {
  width: 20px;  /* Match color preview size */
  height: 20px;  /* Match color preview size */
  cursor: pointer;
  appearance: none;
  background-color: #2a2a2a;
  border: 2px solid #4a4a4a;
  border-radius: 3px;
  position: relative;
  transition: all 0.2s ease;
  flex-shrink: 0;  /* Prevent shrinking */
}

.checkbox-input:hover {
  border-color: #5a5a5a;
  background-color: #323232;
}

.checkbox-input:checked {
  background-color: #0066cc;
  border-color: #0066cc;
}

.checkbox-input:checked::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.checkbox-input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.3);
}

.text-input {
  width: 100%;
  padding: 4px 8px;
  border: 1px solid #3a3a3a;
  border-radius: 4px;
  background-color: #1a1a1a;
  color: #f0f0f0;
  font-size: 12px;
  font-family: 'Consolas', 'Monaco', monospace;
  transition: border-color 0.2s ease;
}

.text-input:hover {
  border-color: #0066cc;
}

.text-input:focus {
  outline: none;
  border-color: #0066cc;
  background-color: #1e1e1e;
}

/* Light theme quaternion adjustments */
.light-theme .quaternion-value label {
  color: #555555;
}

.light-theme .readonly-input {
  background-color: #e8e8e8 !important;
  border-color: #c0c0c0 !important;
  color: #444444 !important;
}

.light-theme .readonly-input:focus {
  border-color: #c0c0c0 !important;
}

.light-theme .checkbox-input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
}

.light-theme .checkbox-input:hover {
  border-color: #b0b0b0;
  background-color: #e8e8e8;
}

.light-theme .checkbox-input:checked {
  background-color: #0066cc;
  border-color: #0066cc;
}

.light-theme .text-input {
  background-color: #f5f5f5;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .text-input:hover {
  border-color: #0066cc;
}

.light-theme .text-input:focus {
  border-color: #0066cc;
  background-color: #efefef;
}

/* Ensure all DraggableNumberInput components are responsive */
.draggable-number-input {
  flex: 1;
  min-width: 0;
  width: 100%;
}

.draggable-number-container {
  display: flex;
  align-items: center;
  gap: 4px;
  width: 100%;
  min-width: 0;
}

/* Material input groups should be flexible */
.material-input-group input[type="range"] {
  width: 100%;
  min-width: 0;
}

/* Property section content should be flexible */
.property-section-content {
  width: 100%;
  min-width: 0;
  overflow: hidden;
}

/* Add Component Button Styles */
.add-component-section {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid #3a3a3a;
  position: relative;
}

.add-component-button {
  width: 50%;
  padding: 6px 10px;
  background-color: #383838;
  border: 1px solid #4a4a4a;
  border-radius: 4px;
  color: #e0e0e0;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 0 auto;
}

.add-component-button:hover {
  background-color: #404040;
  border-color: #5a5a5a;
}

.add-component-button:active {
  background-color: #353535;
}

/* Refresh button for component definitions */
.refresh-components-button {
  background: transparent;
  border: 1px solid #4a4a4a;
  border-radius: 3px;
  color: #e0e0e0;
  font-size: 14px;
  line-height: 1;
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.refresh-components-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: #6a6a6a;
  transform: rotate(180deg);
}

.refresh-components-button:active {
  background-color: rgba(255, 255, 255, 0.15);
}

.component-dropdown {
  position: absolute;
  top: calc(100% + 4px);
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  background-color: #2a2a2a;
  border: 1px solid #4a4a4a;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

.component-search {
  width: 100%;
  padding: 8px 10px;
  background-color: #1e1e1e;
  border: none;
  border-bottom: 1px solid #3a3a3a;
  color: #e0e0e0;
  font-size: 12px;
  outline: none;
  box-sizing: border-box;
}

.component-search:focus {
  background-color: #252525;
  border-bottom-color: #0066cc;
}

.component-search::placeholder {
  color: #888;
}

.component-list {
  max-height: 180px;
  overflow-y: auto;
}

.component-dropdown-item {
  padding: 8px 12px;
  color: #e0e0e0;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.15s ease;
}

.component-dropdown-item:hover {
  background-color: #383838;
}

.component-dropdown-item:active {
  background-color: #404040;
}

.component-dropdown-item.disabled {
  color: #666666;
  cursor: not-allowed;
  font-style: italic;
}

.component-dropdown-item.disabled:hover {
  background-color: transparent;
}

/* Light theme for Add Component */
.light-theme .add-component-section {
  border-top-color: #d0d0d0;
}

.light-theme .add-component-button {
  background-color: #f0f0f0;
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .add-component-button:hover {
  background-color: #e8e8e8;
  border-color: #c0c0c0;
}

.light-theme .add-component-button:active {
  background-color: #e0e0e0;
}

.light-theme .refresh-components-button {
  border-color: #d0d0d0;
  color: #333333;
}

.light-theme .refresh-components-button:hover {
  background-color: rgba(0, 0, 0, 0.05);
  border-color: #b0b0b0;
}

.light-theme .refresh-components-button:active {
  background-color: rgba(0, 0, 0, 0.1);
}

.light-theme .component-dropdown {
  background-color: #ffffff;
  border-color: #d0d0d0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.light-theme .component-search {
  background-color: #f8f8f8;
  border-bottom-color: #e0e0e0;
  color: #333333;
}

.light-theme .component-search:focus {
  background-color: #ffffff;
  border-bottom-color: #0066cc;
}

.light-theme .component-search::placeholder {
  color: #999;
}

.light-theme .component-dropdown-item {
  color: #333333;
}

.light-theme .component-dropdown-item:hover {
  background-color: #f5f5f5;
}

.light-theme .component-dropdown-item:active {
  background-color: #eeeeee;
}

.light-theme .component-dropdown-item.disabled {
  color: #999999;
}

/* Component Context Menu Styles */
.component-context-menu {
  background-color: #242424;
  border: 1px solid #3a3a3a;
  border-radius: 6px;
  padding: 4px;
  min-width: 120px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4), 0 0 1px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  backdrop-filter: blur(10px);
  font-family: system-ui, -apple-system, sans-serif;
}

.context-menu-item {
  padding: 6px 12px;
  color: #e0e0e0;
  font-size: 12px;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.15s ease;
  border-radius: 3px;
  margin: 1px;
}

.context-menu-item:hover:not(.disabled) {
  background-color: #3a7bc8;
  color: #ffffff;
}

.context-menu-item:active:not(.disabled) {
  background-color: #2968b5;
}

.context-menu-item.disabled {
  color: #606060;
  cursor: default;
  font-style: normal;
}

.context-menu-divider {
  height: 1px;
  background-color: #3a3a3a;
  margin: 4px 8px;
}

/* Light theme context menu */
.light-theme .component-context-menu {
  background-color: #ffffff;
  border-color: #e0e0e0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15), 0 0 1px rgba(0, 0, 0, 0.1);
}

.light-theme .context-menu-item {
  color: #333333;
}

.light-theme .context-menu-item:hover:not(.disabled) {
  background-color: #0066cc;
  color: #ffffff;
}

.light-theme .context-menu-item:active:not(.disabled) {
  background-color: #0052a3;
  color: #ffffff;
}

.light-theme .context-menu-item.disabled {
  color: #a0a0a0;
}

.light-theme .context-menu-divider {
  background-color: #e0e0e0;
}

/* Component Buttons */
.component-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 6px;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #3a3a3a;
}

.component-button {
  padding: 6px 12px;
  font-size: 11px;
  font-weight: 500;
  color: #e0e0e0;
  background-color: #383838;
  border: 1px solid #4a4a4a;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.15s ease;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

.component-button:hover {
  background-color: #404040;
  border-color: #555;
  transform: translateY(-1px);
}

.component-button:active {
  background-color: #323232;
  transform: translateY(0);
}

.component-button .button-icon {
  font-size: 12px;
  opacity: 0.8;
}

/* Light theme button styles */
.light-theme .component-buttons {
  border-top-color: #ddd;
}

.light-theme .component-button {
  color: #333;
  background-color: #f0f0f0;
  border-color: #ccc;
}

.light-theme .component-button:hover {
  background-color: #e8e8e8;
  border-color: #aaa;
}

.light-theme .component-button:active {
  background-color: #ddd;
}

/* Responsive adjustments */
@media (max-width: 900px) {
  .property-group > .property-label {
    min-width: 60px !important;
    width: 60px !important;
  }

  .vector-input {
    gap: 2px;
  }

}