.code-panel-container {
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
  isolation: isolate;
  z-index: 10;
}

.code-panel-container * {
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
}

.code-panel-content {
  overflow-y: auto !important;
  overflow-x: auto !important;
  height: calc(100% - 52px); /* Account for header */
  position: relative;
  flex: 1;
  min-height: 0; /* Important for flex overflow */
}

.code-panel-content::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

.code-panel-content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}

.code-panel-content::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.code-panel-content::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.5);
}

/* Dark theme scrollbar */
.code-panel-content.dark::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

.code-panel-content.dark::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
}

.code-panel-content.dark::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

.code-highlight pre {
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
  cursor: text !important;
  pointer-events: auto !important;
}

/* Ensure selection is visible */
.code-highlight pre::selection,
.code-highlight pre *::selection {
  background-color: rgba(51, 153, 255, 0.3);
}

.code-highlight pre::-moz-selection,
.code-highlight pre *::-moz-selection {
  background-color: rgba(51, 153, 255, 0.3);
}