.viewport-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden;
}

/* React Three Fiber canvas wrapper */
.viewport-container > div:not(.viewport-toolbar) {
  flex: 1;
  margin: 0 !important;
  margin-top: 32px !important; /* Account for absolute toolbar - use margin instead of padding to prevent TransformControls offset */
  padding: 0 !important;
}

.viewport-container canvas {
  outline: none;
  display: block;
}

.viewport-toolbar {
  display: flex;
  align-items: center;
  gap: 2px;
  background: #242424;
  padding: 0 8px;
  height: 32px;
  width: 100%;
  border-bottom: 1px solid #1a1a1a;
  box-sizing: border-box;
  z-index: 100;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.toolbar-button {
  padding: 4px 12px;
  background: transparent;
  color: #cccccc;
  border: 1px solid transparent;
  border-radius: 3px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.toolbar-button:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.15);
}

.toolbar-button.active {
  background: #0066cc;
  border-color: #0077dd;
  color: white;
}

.toolbar-separator {
  width: 1px;
  background: #3a3a3a;
  height: 18px;
  margin: 0 8px;
}

/* Light theme styles */
body.light-theme .viewport-toolbar {
  background: #f5f5f5;
  border-bottom: 1px solid #e0e0e0;
}

body.light-theme .toolbar-button {
  color: #333333;
}

body.light-theme .toolbar-button:hover {
  background: rgba(0, 0, 0, 0.05);
  border-color: rgba(0, 0, 0, 0.1);
}

body.light-theme .toolbar-button.active {
  background: #0066cc;
  color: white;
  border-color: #0077dd;
}

body.light-theme .toolbar-separator {
  background: #d0d0d0;
}