.dockview-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.dockview {
  width: 100%;
  height: 100%;
}

/* Panel content styling */
.dockview-panel-content {
  width: 100% !important;
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  min-width: 0;
  box-sizing: border-box;
}

/* Add border to each dockview panel group - no top border */
.dockview .dv-groupview {
  border-left: 1px solid var(--dv-panel-border-color) !important;
  border-right: 1px solid var(--dv-panel-border-color) !important;
  border-bottom: 1px solid var(--dv-panel-border-color) !important;
  border-top: none !important;  /* No top border */
  box-sizing: border-box;
}

/* Ensure panel content doesn't create double borders */
.dockview-panel-content {
  border: none !important;
}

/* Keep internal dividers but remove outer borders that would double with panel border */
.hierarchy-panel .hierarchy-toolbar {
  border-left: none !important;
  border-right: none !important;
  border-top: none !important;
  /* Keep border-bottom for internal divider */
}

.properties-panel > div:first-child,
.settings-panel > div:first-child,
.timeline-panel > div:first-child,
.hyperparameters-panel > div:first-child {
  border: none !important;
}

/* Root dockview container padding for border visibility */
.dockview > .dv-react-part > .split-view-container {
  padding: 1px;  /* Small padding around entire dockview */
  box-sizing: border-box;
}

.viewport-panel,
.node-editor-panel {
  overflow: hidden;
}

/* Ensure all panels are responsive */
.properties-panel,
.hierarchy-panel,
.settings-panel,
.timeline-panel,
.hyperparameters-panel {
  width: 100% !important;
  min-width: 0 !important;
  max-width: 100% !important;
  overflow-x: hidden;
  box-sizing: border-box;
  border: none !important;  /* Remove any internal borders */
}

/* Force dockview panels to be responsive */
.dockview .dv-panel-content,
.dockview .dv-panel-body,
.dockview .panel-body,
.dockview [class*="panel-content"] {
  width: 100% !important;
  min-width: 0 !important;
  max-width: 100% !important;
  padding: 0 !important;
  margin: 0 !important;
}

/* Ensure pane view content has no padding or gaps */
.dockview .dv-paneview,
.dockview .dv-paneview-content {
  padding: 0 !important;
  margin: 0 !important;
  border-top: none !important;
}

/* Remove any border between tabs and content */
.dockview .dv-groupview-container {
  border-top: none !important;
  margin-top: 0 !important;
  padding-top: 0 !important;
}

/* Specific rule for viewport panel to have no padding */
.dockview-panel-content.viewport-panel {
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden;
}

/* Override any inline styles on panel containers */
.dockview [style*="width: 400px"],
.dockview [style*="width:400px"],
.dockview [style*="width: 250px"],
.dockview [style*="width:250px"],
.dockview [style*="width: 350px"],
.dockview [style*="width:350px"] {
  width: 100% !important;
  min-width: 0 !important;
  max-width: 100% !important;
}

/* Dark theme - Custom WiTwin Dark Theme */
.dockview-theme-dark {
  /* Set all tab color variables to same values to remove focus-based styling */
  --dv-activegroup-visiblepanel-tab-background-color: #242424;
  --dv-activegroup-visiblepanel-tab-color: #ffffff;
  --dv-activegroup-hiddenpanel-tab-background-color: #2a2a2a;
  --dv-activegroup-hiddenpanel-tab-color: #909090;

  --dv-inactivegroup-visiblepanel-tab-background-color: #242424;  /* Same as active group */
  --dv-inactivegroup-visiblepanel-tab-color: #ffffff;
  --dv-inactivegroup-hiddenpanel-tab-background-color: #2a2a2a;  /* Same as active group */
  --dv-inactivegroup-hiddenpanel-tab-color: #909090;

  /* Tab container and group colors */
  --dv-tabs-and-actions-container-background-color: #1a1a1a;  /* Slightly adjusted */
  --dv-tabs-container-background-color: #1a1a1a;  /* Darker to contrast with tabs */
  --dv-group-view-background-color: #1e1e1e;

  /* Remove tab dividers and borders */
  --dv-tab-divider-color: transparent;

  /* Panel border color - much darker for better visibility */
  --dv-panel-border-color: rgb(46, 46, 46);

  /* Panel and separator styling */
  --dv-separator-border: 1px solid #3a3a3a;
  --dv-paneview-header-background-color: #252525;
  --dv-paneview-header-border-color: #3a3a3a;
  --dv-group-header-background-color: #252525;
  --dv-group-header-border-color: #3a3a3a;

  /* Drag and drop */
  --dv-drag-over-background-color: rgba(79, 158, 255, 0.1);
  --dv-drag-over-border-color: #0073ff;

  /* Additional overrides */
  --dv-activegroup-header-background-color: #252525;
  --dv-inactivegroup-header-background-color: #202020;
}

/* Remove focus outlines and borders from tabs */
.dockview-theme-dark .dv-default-tab,
.dockview-theme-dark .dv-default-tab:focus,
.dockview-theme-dark .dv-default-tab:focus-visible,
.dockview-theme-dark [role="tab"],
.dockview-theme-dark [role="tab"]:focus,
.dockview-theme-dark [role="tab"]:focus-visible {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

/* Remove text background highlight - ensure tab background is used instead */
.dockview-theme-dark .dv-default-tab > span,
.dockview-theme-dark .dv-default-tab-content,
.dockview-theme-dark [role="tab"] > span {
  background: transparent !important;
  background-color: transparent !important;
}

/* Dark theme - Tab colors only depend on active/inactive state, not focus */
.dockview-theme-dark .dv-tab.dv-active-tab {
  background-color: #242424 !important;  /* Bright - currently visible tab */
  color: #ffffff !important;
}

.dockview-theme-dark .dv-tab.dv-inactive-tab {
  background-color: #1a1a1a !important;  /* Dark - hidden tab */
  color: #909090 !important;
}

/* Hover state for tabs - Dark theme */
.dockview-theme-dark .dv-tab.dv-inactive-tab:hover {
  background-color: #353535 !important;
  color: #d0d0d0 !important;
}

/* Light theme overrides */
.dockview-theme-light {
  /* Set all tab color variables to same values to remove focus-based styling */
  --dv-activegroup-visiblepanel-tab-background-color: #ffffff;
  --dv-activegroup-visiblepanel-tab-color: #222222;
  --dv-activegroup-hiddenpanel-tab-background-color: #e5e5e5;
  --dv-activegroup-hiddenpanel-tab-color: #666666;

  --dv-inactivegroup-visiblepanel-tab-background-color: #ffffff;  /* Same as active group */
  --dv-inactivegroup-visiblepanel-tab-color: #222222;
  --dv-inactivegroup-hiddenpanel-tab-background-color: #e5e5e5;  /* Same as active group */
  --dv-inactivegroup-hiddenpanel-tab-color: #666666;

  /* Tab container and group colors */
  --dv-tabs-and-actions-container-background-color: #d0d0d0;  /* Darker to contrast with tabs */
  --dv-tabs-container-background-color: #d0d0d0;
  --dv-group-view-background-color: #f5f5f5;

  /* Remove tab dividers */
  --dv-tab-divider-color: transparent;

  /* Panel border color - much darker for better visibility */
  --dv-panel-border-color: #808080;

  /* Panel and separator styling */
  --dv-separator-border: 1px solid #dddddd;
  --dv-paneview-header-background-color: #eeeeee;
  --dv-paneview-header-border-color: #dddddd;
  --dv-group-header-background-color: #eeeeee;
  --dv-group-header-border-color: #dddddd;

  /* Drag and drop */
  --dv-drag-over-background-color: rgba(79, 158, 255, 0.1);
  --dv-drag-over-border-color: #4f9eff;
}

/* Light theme - Remove focus outlines and borders from tabs */
.dockview-theme-light .dv-default-tab,
.dockview-theme-light .dv-default-tab:focus,
.dockview-theme-light .dv-default-tab:focus-visible,
.dockview-theme-light [role="tab"],
.dockview-theme-light [role="tab"]:focus,
.dockview-theme-light [role="tab"]:focus-visible {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

/* Light theme - Remove text background highlight */
.dockview-theme-light .dv-default-tab > span,
.dockview-theme-light .dv-default-tab-content,
.dockview-theme-light [role="tab"] > span {
  background: transparent !important;
  background-color: transparent !important;
}

/* Light theme - Tab colors only depend on active/inactive state, not focus */
.dockview-theme-light .dv-tab.dv-active-tab {
  background-color: #ffffff !important;  /* Bright - currently visible tab */
  color: #222222 !important;
}

.dockview-theme-light .dv-tab.dv-inactive-tab {
  background-color: #e5e5e5 !important;  /* Grey - hidden tab */
  color: #666666 !important;
}

/* Light theme - Hover state for tabs */
.dockview-theme-light .dv-tab.dv-inactive-tab:hover {
  background-color: #f0f0f0 !important;
  color: #444444 !important;
}

/* Tab general styling */
.dockview .dv-default-tab {
  padding: 6px 14px;  /* Increased padding for taller tabs */
  font-size: 13px;
  transition: background-color 0.2s, color 0.2s;
  position: relative;
  margin-right: 2px;  /* Small gap between tabs */
}

/* Apply rounded corners to actual tab elements */
.dockview .dv-tab {
  border-radius: 5px 5px 0 0 !important;  /* Slightly smaller radius for taller tabs */
  overflow: hidden;  /* Ensure content respects rounded corners */
}

/* Ensure tab content wrapper also respects styling */
.dockview .dv-tab .dv-default-tab {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 !important;  /* Remove default padding as it's on parent */
  background: transparent !important;  /* Use parent background */
  border: none !important;
  border-radius: inherit !important;
}

/* Tab text styling */
.dockview .dv-tab .dv-default-tab-content {
  font-size: 12px !important;  /* Consistent font size */
  font-weight: 400 !important;  /* Normal weight */
  letter-spacing: 0.3px !important;  /* Slight spacing for readability */
}

/* Remove any separator between tabs */
.dockview .dv-default-tab::after {
  display: none !important;
}

/* Panel header styling */
.dockview .panel-header {
  height: 32px;  /* Match tab height */
  display: flex;
  align-items: center;
  padding: 0 12px;
  background: var(--dv-paneview-header-background-color);
  border-bottom: 1px solid var(--dv-paneview-header-border-color);
}

/* Tab container height adjustment - Similar to UE5 */
.dockview .dv-tabs-and-actions-container {
  height: 32px !important;  /* Increased height for UE5-like tabs */
  margin-bottom: 0 !important;  /* Remove any bottom margin */
}

/* Dark theme - border matches active tab color */
.dockview-theme-dark .dv-tabs-and-actions-container {
  border-bottom: 2px solid #242424 !important;  /* Same as active tab background */
}

/* Light theme - border matches active tab color */
.dockview-theme-light .dv-tabs-and-actions-container {
  border-bottom: 2px solid #ffffff !important;  /* Same as active tab background */
}

/* Remove any gap between tabs and content */
.dockview .dv-groupview-header {
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
}

.dockview .dv-paneview {
  margin-top: 0 !important;
  padding-top: 0 !important;
}

/* Ensure tab height matches container */
.dockview .dv-tab {
  height: 28px !important;  /* Taller tabs like UE5 */
  line-height: 28px !important;
  padding: 0 14px !important;  /* Slightly more padding */
  margin: 2px 2px 0 2px !important;  /* Small margin for spacing between tabs */
  display: inline-flex !important;
  align-items: center !important;
  font-size: 13px !important;
}

/* First tab doesn't need left margin */
.dockview .dv-tab:first-child {
  margin-left: 0 !important;
}

/* Remove border between tabs and content area for cleaner look */
.dockview .dv-groupview-content {
  border-top: none !important;
}

/* Ensure tabs container has proper padding */
.dockview .dv-tabs-container {
  padding-left: 4px !important;
}

/* Tab container background - prevent drag but allow drop */
.dockview .dv-tabs-and-actions-container {
  pointer-events: auto !important;  /* Allow drop events */
  -webkit-user-select: none !important;
  user-select: none !important;
}

/* Prevent dragging from tab container by making it non-draggable */
.dockview .dv-tabs-container {
  pointer-events: auto !important;
  -webkit-user-select: none !important;
  user-select: none !important;
  /* Override any draggable attribute */
  -webkit-user-drag: none !important;
  -moz-user-drag: none !important;
  user-drag: none !important;
}

/* Disable drag on the group header but allow drop */
.dockview .dv-groupview-header,
.dockview .dv-tabs-and-actions-container > div:not(.dv-tab) {
  -webkit-user-drag: none !important;
  -moz-user-drag: none !important;
  user-drag: none !important;
  cursor: default !important;
}

/* Only individual tabs can be dragged */
.dockview .dv-tab {
  pointer-events: auto !important;
  /* cursor: grab !important; */
  position: relative;
  z-index: 10;  /* Ensure tabs are above other elements */
}

.dockview .dv-tab:active {
  cursor: grabbing !important;
}

/* Make sure empty space in tab bar cannot initiate drag */
.dockview .dv-tabs-container::after {
  content: '';
  flex: 1;
  pointer-events: auto;  /* Can receive drops */
  -webkit-user-drag: none !important;
  user-drag: none !important;
}

/* Ensure the tab container itself cannot be the drag source */
.dockview .dv-tabs-and-actions-container[draggable="true"],
.dockview .dv-tabs-container[draggable="true"] {
  -webkit-user-drag: none !important;
  -moz-user-drag: none !important;
  user-drag: none !important;
}

/* Action buttons remain interactive */
.dockview .dv-group-actions {
  pointer-events: auto !important;
}

.dockview .dv-default-tab-actions {
  pointer-events: auto !important;
}

/* Resize handles */
.dockview .split-view-container .sash {
  background-color: transparent;
  transition: background-color 0.2s;
}

.dockview .split-view-container .sash:hover {
  background-color: var(--dv-drag-over-border-color);
}

.dockview .split-view-container .sash.vertical {
  width: 4px;
  margin-left: -2px;
}

.dockview .split-view-container .sash.horizontal {
  height: 4px;
  margin-top: -2px;
}

/* Floating group styling */
.dockview .dv-floating-box {
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.dockview-theme-dark .dv-floating-box {
  background-color: #1e1e1e;
  border: 1px solid #3a3a3a;
}

.dockview-theme-light .dv-floating-box {
  background-color: #ffffff;
  border: 1px solid #dddddd;
}

/* Watermark styling */
.dockview .watermark {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  opacity: 0.5;
}

.dockview-theme-dark .watermark {
  color: #666666;
}

.dockview-theme-light .watermark {
  color: #aaaaaa;
}

/* Group actions */
.dockview .group-actions {
  display: flex;
  align-items: center;
  padding: 0 8px;
}

.dockview .group-actions button {
  background: none;
  border: none;
  color: inherit;
  /* cursor: pointer; */
  padding: 4px;
  margin-left: 4px;
  border-radius: 3px;
  transition: background-color 0.2s;
}

.dockview-theme-dark .group-actions button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.dockview-theme-light .group-actions button:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

/* Scrollbar styling */
.dockview-theme-dark ::-webkit-scrollbar {
  background-color: #1e1e1e;
}

.dockview-theme-dark ::-webkit-scrollbar-thumb {
  background-color: #4a4a4a;
}

.dockview-theme-dark ::-webkit-scrollbar-thumb:hover {
  background-color: #5a5a5a;
}

.dockview-theme-light ::-webkit-scrollbar {
  background-color: #f5f5f5;
}

.dockview-theme-light ::-webkit-scrollbar-thumb {
  background-color: #c0c0c0;
}

.dockview-theme-light ::-webkit-scrollbar-thumb:hover {
  background-color: #a0a0a0;
}