.console-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--bg-primary);
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 12px;
}

/* Header with same color as tabs */
.console-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  background: #242424;
  border-bottom: 1px solid var(--border-color);
  min-height: 36px;
}

.light-theme .console-header {
  background: #FFFFFF;
  border-bottom: 1px solid #E0E0E0;
}

.console-clear-btn {
  padding: 4px 12px;
  background: #1a1a1a;
  border: 1px solid #333;
  color: #CCCCCC;
  cursor: pointer;
  border-radius: 4px;
  font-size: 12px;
  transition: all 0.2s;
}

.console-clear-btn:hover {
  background: #2a2a2a;
  border-color: #444;
  color: #FFFFFF;
}

.light-theme .console-clear-btn {
  background: #F0F0F0;
  border: 1px solid #D0D0D0;
  color: #333;
}

.light-theme .console-clear-btn:hover {
  background: #E0E0E0;
  border-color: #C0C0C0;
  color: #000;
}

/* Filters */
.console-filters {
  display: flex;
  gap: 16px;
  align-items: center;
}

.console-filter {
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  user-select: none;
  color: #CCCCCC;
  transition: opacity 0.2s;
}

.console-filter.inactive {
  opacity: 1;
}

.console-filter.inactive span {
  color: #CCCCCC;
}

.console-filter input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  margin: 0;
  appearance: none;
  -webkit-appearance: none;
  background: #1a1a1a;
  border: 1px solid #444;
  border-radius: 3px;
  position: relative;
  transition: all 0.2s;
}

.console-filter input[type="checkbox"]:checked {
  background: #0066CC;
  border-color: #0066CC;
}

.console-filter input[type="checkbox"]:checked::after {
  content: '';
  position: absolute;
  top: -1px;
  left: 2px;
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.console-filter input[type="checkbox"]:hover {
  border-color: #0066CC;
}

.console-filter span {
  font-size: 12px;
  font-weight: 500;
}

.light-theme .console-filter {
  color: #333;
}

.light-theme .console-filter.inactive span {
  color: #333;
}

.light-theme .console-filter input[type="checkbox"] {
  background: #FFFFFF;
  border: 1px solid #CCC;
}

.light-theme .console-filter input[type="checkbox"]:checked {
  background: #0066CC;
  border-color: #0066CC;
}

.light-theme .console-filter input[type="checkbox"]:hover {
  border-color: #0066CC;
}

/* Messages area */
.console-messages {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
  background: var(--bg-primary);
}

.console-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--text-secondary);
  opacity: 0.6;
}

.console-empty p {
  margin: 4px 0;
}

.console-hint {
  font-size: 11px;
  font-style: italic;
}

/* Text selection colors */
.console-message ::selection {
  background: #3A3A3A;
  color: #FFFFFF;
}

.console-message ::-moz-selection {
  background: #3A3A3A;
  color: #FFFFFF;
}

.light-theme .console-message ::selection {
  background: #C5C5C5;
  color: #000000;
}

.light-theme .console-message ::-moz-selection {
  background: #C5C5C5;
  color: #000000;
}

/* Message styles */
.console-message {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  padding: 2px 4px;
  margin-bottom: 2px;
  border-radius: 2px;
  transition: background 0.1s;
  line-height: 1.4;
  word-break: break-word;
  cursor: text;
}

.console-message:hover {
  background: var(--bg-secondary);
}

.console-message-icon {
  width: 16px;
  text-align: center;
  flex-shrink: 0;
  user-select: none;
}

.console-message-time {
  color: var(--text-secondary);
  opacity: 0.7;
  font-size: 11px;
  flex-shrink: 0;
}

.console-message-time.no-select {
  user-select: none;
}

.console-message-source {
  color: var(--text-secondary);
  font-weight: 600;
  flex-shrink: 0;
  user-select: text;
}

.console-message-text {
  flex: 1;
  white-space: pre-wrap;
  user-select: text;
}

/* Level-specific colors */
.console-log .console-message-icon {
  color: var(--text-secondary);
}

.console-log .console-message-text {
  color: var(--text-primary);
}

.console-warning .console-message-icon {
  color: #FFA500;
}

.console-warning .console-message-text {
  color: #FFA500;
}

.console-error .console-message-icon {
  color: #FF6B6B;
}

.console-error .console-message-text {
  color: #FF6B6B;
}

/* Input area */
.console-input-wrapper {
  display: flex;
  align-items: center;
  padding: 12px;
  background: var(--bg-primary);
  border-top: 1px solid var(--border-color);
  gap: 8px;
}

.console-input-container {
  flex: 1;
  display: flex;
  align-items: center;
  background: #1A1A1A;
  border: 1px solid #2A2A2A;
  border-radius: 6px;
  padding: 6px 10px;
  gap: 6px;
  transition: all 0.2s;
}

.console-input-container:focus-within {
  border-color: #0066CC;
  box-shadow: 0 0 0 1px rgba(0, 102, 204, 0.2);
}

.console-prompt {
  color: #888;
  font-weight: bold;
  user-select: none;
  flex-shrink: 0;
}

.console-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: #FFFFFF;
  font-family: inherit;
  font-size: 12px;
  padding: 2px 4px;
}

.console-input::placeholder {
  color: #666;
  opacity: 1;
}

.light-theme .console-input-container {
  background: #ECECEC;
  border: 1px solid #D6D6D6;
}

.light-theme .console-input-container:focus-within {
  border-color: #0066CC;
  box-shadow: 0 0 0 1px rgba(0, 102, 204, 0.15);
}

.light-theme .console-prompt {
  color: #999;
}

.light-theme .console-input {
  color: #333;
}

.light-theme .console-input::placeholder {
  color: #AAA;
}

/* Scrollbar styling */
.console-messages::-webkit-scrollbar {
  width: 8px;
}

.console-messages::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

.console-messages::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

.console-messages::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary);
}

/* Light theme adjustments */
.light-theme .console-panel {
  background: #FFFFFF;
}

.light-theme .console-messages {
  background: #FFFFFF;
}

.light-theme .console-message:hover {
  background: #F9F9F9;
}

.light-theme .console-input-wrapper {
  background: #FFFFFF;
  border-top: 1px solid #E0E0E0;
}

/* Context Menu */
.console-context-menu {
  background: #2A2A2A;
  border: 1px solid #3A3A3A;
  border-radius: 4px;
  padding: 4px 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  min-width: 100px;
}

.console-context-menu-item {
  display: block;
  width: 100%;
  padding: 6px 16px;
  background: transparent;
  border: none;
  color: #CCCCCC;
  text-align: left;
  cursor: pointer;
  font-size: 12px;
  transition: background 0.1s;
}

.console-context-menu-item:hover {
  background: #3A3A3A;
  color: #FFFFFF;
}

.light-theme .console-context-menu {
  background: #FFFFFF;
  border: 1px solid #D0D0D0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.light-theme .console-context-menu-item {
  color: #333;
}

.light-theme .console-context-menu-item:hover {
  background: #F0F0F0;
  color: #000;
}